import React from 'react';
import {useLazyQuery} from "@apollo/client";
import {STOP} from "../../store/Queries";
import {showInfo} from "../../util/ShowError";
import OnlineChannelItem from "../../items/OnlineChannelItem";
import PlayerModal from "../../modal/PlayerModal";


const OnlineChannelsList = ({onlineChannels}) => {


    const [stopQuery] = useLazyQuery(STOP, {fetchPolicy: 'network-only'})


    const handleStop = (id) => {
        stopQuery({variables: {id: id}})
            .then(r => showInfo(`Channel ID ${r.data.stop} stopped`))
    }

    const [playerModalOpen, setPlayerModalOpen] = React.useState({isOpen: false, channelId: 0,channelName:''})


    const openPlayerModal = (channelId,channelName) => {
        setPlayerModalOpen({isOpen: true, channelId: channelId,channelName: channelName})
    }


    return (
        <>
            <PlayerModal show={playerModalOpen.isOpen} title={<p>Playing <span className={'text-success'}>{playerModalOpen.channelName}</span></p>}
                       handleClose={() => setPlayerModalOpen({isOpen: false, channelId: playerModalOpen.channelId,channelName: playerModalOpen.channelName})}
                       id={playerModalOpen.channelId}/>

            <div className={'list'} style={{
                maxHeight: 'calc(100vh - 100px)',
                overflowY: 'auto',
                overflowX: 'hidden',
                paddingRight: 10
            }}>
                {onlineChannels.map((i, k) => (
                        <OnlineChannelItem key={k} channel={i} watch={openPlayerModal} stop={handleStop}/>
                    )
                )
                }
            </div>
        </>
    );
};

export default OnlineChannelsList;
