import React from 'react';
import {Col, ListGroup, Modal, Row} from "react-bootstrap";
import {gql, useMutation, useQuery} from "@apollo/client";
import {ADD_KEY, DEL_KEY} from "../store/Mutation";
import {useFormik} from "formik";
import {GET_CHANNEL_KEYS} from "../store/Queries";
import KeyItem from "../items/KeyItem";

const KeysModal = ({show, handleClose, title, channelId}) => {

    const formik = useFormik({
        initialValues: {
            kid: '',
            key: ''
        },
        onSubmit: values => {
            addKeyMut({variables: {model: {...values, channelId: channelId}}})
                .then(r => console.log('RES', r))

        },
    })

    const {data} = useQuery(GET_CHANNEL_KEYS, {variables: {channelId: channelId}})

    const [addKeyMut] = useMutation(ADD_KEY, {
        update(cache, {data: {addKey}}) {
            cache.modify({
                fields: {
                    keys(existing = []) {

                        const newKeyFragment = cache.writeFragment({
                            data: addKey,
                            fragment: gql`
                                fragment NewKey on KeyEntry {
                                    id
                                }
                            `
                        });
                        return [newKeyFragment, ...existing];
                    }
                }
            })
        }
    })

    const [delKeyMut] = useMutation(DEL_KEY, {
        update(cache, {data: {delKey}}) {
            cache.modify({
                fields: {
                    keys(existing = [], {readField}) {
                        return existing.filter(
                            keyRef => delKey !== readField('id', keyRef),
                        );
                    }
                }
            })
        }
    })

    const handleDelete = (id) => {
        delKeyMut({variables: {id: id}})
    }


    return (
        <Modal show={show} className={'fade'} size='lg'>
            <div className="modal-content">
                <div className="modal-header">
                    <h5 className="modal-title" id="exampleModalLabel1">{title}</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"
                            onClick={handleClose}/>
                </div>
                <div className="modal-body">
                    <Row>
                        <Col sm={12}>
                            <div className="card bg-transparent mb-3">

                                <h5 className="card-title">Existing keys</h5>
                                <ListGroup>
                                    {data && data.keys.map((i, k) => (
                                        <KeyItem key={k} item={i} deleteKey={handleDelete}/>
                                    ))}
                                </ListGroup>

                            </div>
                        </Col>
                        <Col sm={12}>
                            <h5 className="card-title">Add key</h5>
                            <div className="card bg-transparent mb-3" style={{border:'1px solid rgba(255, 255, 255, 0.12)'}}>

                                <div className="card-body" style={{backgroundColor:'rgba(0,0,0,.2)'}}>
                                    <Row>
                                        <Col sm={12}>

                                            <form>
                                                <Row className='mb-3'>
                                                    <Col sm={6}>
                                                        <label className="form-label"
                                                               htmlFor="basic-default-kid">Kid</label>
                                                        <input type="text" name={'kid'} maxLength={32}
                                                               className="form-control"
                                                               id="basic-default-kid"
                                                               value={formik.values.kid}
                                                               onChange={formik.handleChange}/>
                                                    </Col>
                                                    <Col sm={6}>
                                                        <label className="form-label"
                                                               htmlFor="basic-default-key">Key</label>
                                                        <input type="text" name={'key'} maxLength={32}
                                                               className="form-control"
                                                               id="basic-default-key"
                                                               onChange={formik.handleChange}/>
                                                    </Col>
                                                </Row>
                                            </form>
                                        </Col>
                                        <Col sm={12} className='text-center'>
                                            <div className='d-flex'>
                                            <button style={{width:'100%'}} type="button" className="btn btn-primary"
                                                    onClick={formik.handleSubmit}>
                                                <span className='bx bx-plus'/>
                                                &nbsp;Assign
                                            </button>
                                            </div>
                                        </Col>
                                    </Row>
                                </div>
                            </div>
                        </Col>
                    </Row>
                </div>
            </div>

        </Modal>
    );
};

export default KeysModal;
