import React from 'react';
import {useQuery} from "@apollo/client";
import {ACTIVE_CHANNELS} from "../store/Queries";
import OnlineChannelsList from "../component/list/OnlineChannelsList";
import DashboardHeader from "../component/DasboardHeader";
import {useDebouncedCallback} from "use-debounce";

const Dev = () => {

    const [data,setData] = React.useState()
    const [filtered,setFiltered] = React.useState([])
    const {startPolling, stopPolling} = useQuery(ACTIVE_CHANNELS,{onCompleted:(data) => setData(data)} )

    const [counts, setCounts] = React.useState({online:0,buffering:0,total:0})

    const [isSearch,setIsSearch] = React.useState(false)


    React.useEffect(() => {
        startPolling(1000)
        return () => stopPolling()
    }, [])

    React.useEffect(() => {
        if (data?.activeChannels) {
            let online = data.activeChannels.filter((ch) => ch.status.progress === "continue").length
            let buffering = data.activeChannels.filter((ch) => ch.status.progress === "buffering").length
           // let waiting = data.activeChannels.filter((ch) => ch.state.name !== "Online" && ch.state.name !== "Buffering").length
            setCounts({online: online,buffering: buffering,total: online + buffering})
            if (!isSearch) {
                setFiltered(data.activeChannels)
            }
        }
    }, [data?.activeChannels,isSearch])

    const debounced = useDebouncedCallback(
        // function
        (value) => {
            if (value) {
                setIsSearch(true)
                let filtered = data?.activeChannels.filter((ch) => ch.id == value || ch.name.includes(value))

                setFiltered(filtered);
            } else {
                setIsSearch(false)
            }

        },
        // delay in ms
        500
    );

    return (
        <>
            <DashboardHeader debounce={debounced} counts={counts}/>
            <div className='content-wrapper'>
                <div className='container-fluid'>
                    <OnlineChannelsList onlineChannels={filtered || []}/>
                </div>
            </div>
        </>
    );
};

export default Dev;
