import React from 'react';
import {Badge, Button, Col, Row} from "react-bootstrap";
import ReactCountryFlag from "react-country-flag";
import ChannelDropdown from "../component/ChannelDropdown";
import {langToCountryCode} from "../util/LangToCuntryCode";

const ChannelItem = ({channel, handleDelete, openKeysModal, handleCopy, handleEdit,play,stop,online,handleInfo}) => {

    return (

                <div className={'card mb-3'}>
                    <Row className='align-items-center'>

                        <Col sm={1} className={`text-center ${online && 'text-success'}`}>
                            <span className='bx bx-tv'/>
                        </Col>
                        <Col>{channel.id}</Col>
                        <Col>
                            {channel.name}
                        </Col>
                        <Col>
                            {channel.group}
                        </Col>
                        <Col>
                            {channel.provider}
                        </Col>
                        <Col style={{fontSize:'1.8rem'}}>
                            {channel.lang && <ReactCountryFlag countryCode={langToCountryCode(channel.lang)}/>}
                        </Col>
                        <Col>
                            <i className='bx bx-key me-1' />
                            <Badge bg={channel.keysCount > 0 ? 'success':'danger'}>{channel.keysCount}</Badge>
                        </Col>
                        {/*<Col>*/}
                        {/*    <Button variant='info' onClick={() => info(channel.id)}><span className='bx bx-info-circle' />Info</Button>*/}
                        {/*</Col>*/}
                        <Col>
                            {online ?
                                <Button variant='danger' onClick={() => stop(channel.id)}>
                                    <span className='bx bx-stop' />Stop</Button>
                                :
                                <Button variant='success' onClick={() => play(channel.id)}>
                                    <span className='bx bx-play-circle' />Play</Button>

                            }
                        </Col>
                        <div className='d-flex justify-content-center' style={{width:70}}>

                            <ChannelDropdown openKeysModal={() => openKeysModal(channel.id)}
                                             handleEdit={() => handleEdit(channel)}
                                             handleCopy={() => handleCopy(channel)}
                                             handleDelete={() => handleDelete(channel.id)}
                                             handleInfo={() => handleInfo(channel.id)}
                            />

                        </div>
                    </Row>
                </div>
    );
};

export default ChannelItem;
